import React from "react";
import './TableMotorStatus.css';
import PropTypes from 'prop-types';

class TableMotorStatus extends React.Component {

    getCellClassName(value) {
        const numvalue = Number(value);
        if (numvalue >= 75) {
            return 'pd-cell pd-cell-red';
        }
        else if (numvalue >= 25) {
            return 'pd-cell pd-cell-amber';
        }
        else {
            return 'pd-cell pd-cell-green';
        }
    }

    getTD(value) {
        const numvalue = Number(value);
        return <td className={this.getCellClassName(numvalue)}>{numvalue}</td>
    }

    render() {

        if (undefined !== this.props.data) {
            const rows = [];
            for (var i = 0; i < this.props.data.length; i++) {
                const cells = [];
                cells.push(<td className="date">{this.props.data[i].Date}</td>)
                cells.push(this.getTD(this.props.data[i].Cable));
                cells.push(this.getTD(this.props.data[i].Machine));
                cells.push(this.getTD(this.props.data[i].Local));
                cells.push(this.getTD(this.props.data[i].Acoustic));
                rows.push(<tr>{cells}</tr>)
            }

            return <table id="simple-board">
                <thead>
                    <tr>
                        <th className="date">Date</th>
                        <th className="pd-cell">Cable</th>
                        <th className="pd-cell">Machine</th>
                        <th className="pd-cell">Local</th>
                        <th className="pd-cell">Acoustic</th>
                    </tr>
                </thead>
                <tbody>
                    {rows}
                </tbody>
            </table>;

        }
        else {
            return <div></div>;
        }
    }
}

TableMotorStatus.propTypes = {
    data: PropTypes.array
};

export default TableMotorStatus;